.industry {
  font-family: 'Inter', sans-serif;
  // background-color: $whitesmoke;

  &__title {
    // font-size: 128px;
    // font-style: normal;
    // font-weight: 700;
    // line-height: 100%; /* 128px */
    // letter-spacing: -6.4px;
    // color: $black;
    // margin: 0;
    // @include bp(tablet) {
    //   font-size: 48px;
    //   letter-spacing: -2.4px;
    // }
  }

  &__copy {
    padding-top: 48px;
    // font-size: 20px;
    // font-style: normal;
    // font-weight: 500;
    // line-height: 140%; /* 28px */
    // letter-spacing: -0.6px;
    // color: $black;
  }

  &__figure {
    width: 100%;

    &--desktop {
      display: block;
      @include bp(phone) {
        display: none;
      }
    }

    &--mobile {
      display: none;
      @include bp(phone) {
        display: block;
      }
    }
  }

  &__categories {
    display: flex;
    justify-content: space-between;
    color: $whitesmoke;
    font-size: 32px;
    font-style: normal;
    font-weight: 700;
    line-height: 120%; /* 38.4px */
    letter-spacing: -1.6px;
    background-color: $whitesmoke;

    @include bp(tablet) {
      flex-direction: column;
    }
  }

  &__category {
    width: 100%;
    border-right: 1px solid $black;
    border-top: 1px solid $black;
    border-bottom: 1px solid $black;

    @include bp(phone) {
      border-right: none;
    }

    &-items {
      display: flex;
      justify-content: flex-start;
      place-items: center;
      padding: 0 64px;
      min-height: 170px;
      background-color: $black;

      @include bp(phone) {
        padding: 0 1.5rem;
      }

      & p {
        padding-right: 64px;
      }
    }

    &-item {
      font-size: 26px;
      line-height: 115%;
      letter-spacing: -0.6px;
      color: $whitesmoke;
    }

    &-bullets {
      display: flex;
      justify-content: space-between;
      flex-direction: column;
      padding: 64px;
      background-color: $whitesmoke;

      @include bp(phone) {
        padding: 64px 30px;
      }

      // &-border {
      //   // border-left: 1px solid $black;
      //   border-right: 1px solid $black;
      //   border-top: 1px solid $black;
      //   border-bottom: 1px solid $black;
      // }

      &__items {
        display: flex;
        justify-content: flex-start;
        gap: 20px;
        padding: 10px 0;
      }

      &__arrow {
        margin: -8px 0;
      }

      &__item {
        font-size: 18px;
        font-style: normal;
        font-weight: 400;
        letter-spacing: -0.72px;
        // padding-top: 10px;
        color: $black;
      }

      &__subitem {
        list-style: disc;

        & li {
          font-size: 1rem;
          margin-left: 1rem;
          font-weight: 400;
          color: $black;
          letter-spacing: -0.7px;
        }

        // & li:last-child {
        //   padding-bottom: 20px;
        // }
      }

      &__arrow img {
        width: 20px;
      }
    }
  }
}
